___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Franklin Gothic Medium', Arial, sans-serif;
}

td {
    border: 1px solid black;
    padding: 8px;
    width: 40px;
    height: 40px;
}

table {
  border-spacing: 4px;
}

#starterDiv {
  display: flex;
  height: 80vh;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#newStarterDiv {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#starter {
  border: 5px solid red;
  font-size: 50px;
}

#parent {
  display: flex;
  height: 90vh;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.computerCell {
  width: 40px;
  height: 40px;
  font-size: 30px;
}

#userDisplay, #computerDisplay {
  height: 30px;
  font-size: 30px;
}

#userDiv, #computerDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
}

td.ship {
  background-color: rgb(116, 229, 116);
}

.goodHit {
  background-color: red;
}

.badHit {
  background-color: rgb(148, 187, 228);
}

#lost {
  padding-top: 40px;
  text-align: center;
  font-size: 30px;
  color: red;
}

#won {
  padding-top: 40px;
  text-align: center;
  font-size: 30px;
  color: green;
